.app {
  height: 100vh;

  .sections {
    width: 100%;
    height: calc(100vh - 70px);
    position: relative;
    top: 70px;
    scroll-behavior: smooth;
    overflow-x: hidden;
    scroll-snap-type: y mandatory;
    scrollbar-width: none; //for firefox
    &::-webkit-scrollbar{display: none}

    > * {
      width: 100vw;
      height: calc(100vh - 70px);
      scroll-snap-align: start;
      overflow: hidden;
    }
  }

}

@media (max-width: 668px) {
  .app{
    .sections{
      height: calc(100vh - 50px);
      top: 50px;

    > * {
      height: calc(100vh - 50px);

    }

  }
}
}